.charter-boats_title {
  font-size: 22px;
  font-weight: 500;
  line-height: 30px;
  margin-bottom: 20px;
}

.charter-boats_wrap {
  display: flex;
  flex-wrap: wrap;
}

.charter-boats-item {
  width: 31.8%;
  margin-right: 15px;
  margin-bottom: 25px;
}

.charter-boats-item:nth-child(3n) {
  margin-right: 0;
}

.charter-boats-item:hover img {
  transform: scale(1.1);
}

.charter-boats-item .thumb-wrap {
  display: block;
  height: 185px;
  margin-bottom: 15px;
  border-radius: 4px;
  overflow: hidden;
}

.charter-boats-item .thumb-wrap img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  transition: .3s all ease;
}

.charter-boats-item .description-wrap h4 a {
  font-size: 18px;
  font-weight: 500;
  line-height: 28px;
  color: #1F283C;
  margin-bottom: 15px;
}

.charter-boats-item .description-wrap .rate-rows p {
  display: block;
  font-size: 15px;
  line-height: 22px;
  color: #23A8CD;
}

.charter-boats-item .description-wrap .people-count {
  display: block;
  font-size: 15px;
  line-height: 22px;
}

@media screen and (max-width: 1200px) {
  .charter-boats-item {
    width: 31.7%;
  }
}

@media screen and (max-width: 500px) {
  .charter-boats_wrap {
    justify-content: space-between;
  }
  .charter-boats_title {
    font-size: 21px;
    line-height: 30px;
  }
  .charter-boats-item {
    width: 48%;
    margin-right: 0;
  }
  .charter-boats-item .thumb-wrap {
    height: 140px;
    margin-bottom: 10px;
  }
  .charter-boats-item .description-wrap h4 a {
    font-size: 16px;
    line-height: 26px;
  }
  .charter-boats-item .description-wrap .rate-rows p {
    font-size: 14px;
    line-height: 20px;
  }
  .charter-boats-item .description-wrap .people-count {
    font-size: 14px;
    line-height: 20px;
  }
}

